import { DateWiseData } from './../models/date-wise-data';
import { GlobalDataSummary } from './../models/global-data';
import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { map, catchError } from 'rxjs/operators';
import { Observable } from 'rxjs';  
import { error } from 'protractor';


@Injectable({
  providedIn: 'root'
})
export class DataService {

  private baseURL = 'https://raw.githubusercontent.com/CSSEGISandData/COVID-19/master/csse_covid_19_data/csse_covid_19_daily_reports/';
  private globalDataURL = '';
  private dateWiseDataUrl = 'https://raw.githubusercontent.com/CSSEGISandData/COVID-19/master/csse_covid_19_data/csse_covid_19_time_series/time_series_covid19_confirmed_global.csv';
  private extension = '.csv';
  month;
  date;
  year;
  getDate(date: number){
    if( date <10 ){
      return '0'+date;
    }
    else{
      return date;
    }
  }
  
  constructor( private http : HttpClient ) {
    let now = new Date();
    this.month = now.getMonth() + 1;
    this.date = now.getDate();
    this.year = now.getFullYear();
    
    this.globalDataURL = `${this.baseURL}${this.getDate(this.month)}-${this.getDate(this.date)}-${this.year}${this.extension}`;
    // console.log(this.globalDataURL); 

   } 

   getDateWiseData() {
    return this.http.get(this.dateWiseDataUrl, { responseType: 'text' })
      .pipe(map(result => {
        let rows = result.split('\n'); 
        // console.log(rows);
        let mainData = {};
        let header = rows[0];
        let dates = header.split(/,(?=\S)/)
        dates.splice(0 , 4);
        rows.splice(0 , 1);
        rows.forEach(row=>{
          let cols = row.split(/,(?=\S)/)
          let con = cols[1];
          cols.splice(0 , 4);
          // console.log(con , cols);
          mainData[con] = [];
          cols.forEach((value , index)=>{
            let dw : DateWiseData = {
              cases : +value ,
              country : con , 
              date : new Date(Date.parse(dates[index])) 

            }
            mainData[con].push(dw)
          })
          
        })


        // console.log(mainData);
        return mainData;
      }))
  }


  getGolbalData(){
    return this.http.get(this.globalDataURL, { responseType :  'text'}).pipe(
      map(result=>{
        let data : GlobalDataSummary[] = [];
        let rows = result.split('\n');
        rows.splice(0, 1);
        let raw = {};
        // console.log(rows);
        rows.forEach(rows=>{ 
          
          let cols = rows.split(/,(?=\S)/);
          // console.log(cols);
          let cs = {
            country : cols[3],
            confirmed : +cols[7],
            deaths : +cols[8],
            recovered : +cols[9],
            active : +cols[10]
          }
          let temp : GlobalDataSummary = raw[cs.country];
          if(temp){
            temp.active = cs.active + temp.active;
            temp.confirmed = cs.confirmed + temp.confirmed;
            temp.deaths = cs.deaths + temp.deaths;
            temp.recovered = cs.recovered + temp.recovered;
            raw[cs.country] = temp;
          }
          else{
            raw[cs.country] = cs;
          }
        })
        // console.log(raw); 

        return <GlobalDataSummary[]>Object.values(raw);
      }),
      catchError((error : HttpErrorResponse)=>{
        if( error.status == 404 ){
          this.date = this.date - 1;
          this.globalDataURL = `${this.baseURL}${this.getDate(this.month)}-${this.getDate(this.date)}-${this.year}${this.extension}`;
          // console.log(this.globalDataURL); 
          return this.getGolbalData();
        }
    })
    )
    
  }  



}
