import { GlobalDataSummary } from './../../models/global-data';
import { DataService } from './../../services/data.service';
import { Component, OnInit, ElementRef} from '@angular/core';
import { Chart } from 'chart.js'


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  chart = [];
  list = [];
  con = [];

  totalConfirmed = 0;
  totalActive = 0;
  totalDeaths = 0;
  totalRecovered = 0;
  globalData : GlobalDataSummary[];
  condata : GlobalDataSummary[];
  cofdata : GlobalDataSummary[];

  


  constructor( private ds : DataService,
    private elementRef: ElementRef ) { }

  
  country(){
    this.ds.getGolbalData().subscribe({next : (result) => {
      this.condata = result;
      result.forEach(cs=>{
        if(cs.confirmed > 200000)
        this.list.push(cs.country)
      })
      console.log(this.list)
    }})
    return this.list;
    
  }

  confirmed(){
    this.ds.getGolbalData().subscribe({next : (result) => {
      this.condata = result;
      result.forEach(cs=>{
        if(cs.confirmed > 200000)
        this.con.push(cs.confirmed)
      })
      console.log(this.con)
    }})
    return this.con;
     
  }

    


  ngOnInit(): void {

    // this.country();
    // this.confirmed();

    this.ds.getGolbalData()
    .subscribe({
      next : (result)=> {
        // console.log(result);
        this.globalData = result;
        result.forEach(cs=> {
          if( !Number.isNaN(cs.confirmed) ) {
            this.totalConfirmed += cs.confirmed,
            this.totalActive += cs.active,
            this.totalDeaths += cs.deaths,
            this.totalRecovered += cs.recovered
          }
        })
      }
    })

  var myChart = new Chart("canvas", {
    type: 'bar',
    data: {
        labels: this.country(),
        datasets: [{
            label: '# of Votes',
            data: this.confirmed(),
            backgroundColor: [
                'rgba(255, 99, 132, 0.2)',
                'rgba(54, 162, 235, 0.2)',
                'rgba(255, 206, 86, 0.2)',
                'rgba(75, 192, 192, 0.2)',
                'rgba(153, 102, 255, 0.2)',
                'rgba(255, 159, 64, 0.2)'
            ],
            borderColor: [
                'rgba(255, 99, 132, 1)',
                'rgba(54, 162, 235, 1)',
                'rgba(255, 206, 86, 1)',
                'rgba(75, 192, 192, 1)',
                'rgba(153, 102, 255, 1)',
                'rgba(255, 159, 64, 1)'
            ],
            borderWidth: 1
        }]
    },
    options: {
        scales: {
            yAxes: [{
                ticks: {
                    beginAtZero: true
                }
            }]
        }
    }
});
 
  
}
 
}
